<template>
    <view>
        <view class="bonus-wrap">
            <view class="bonus-list">
                <view
                    class="bonus-box"
                    v-for="(item,index) in rdata.params.list"
                    :key="index"
                >
                    <view class="l">
                        <view><text>￥</text>{{item.amount}}</view>
                    </view> 
                    <view class="m">
                        <view class="mm">
                            <view>{{item.name}}</view>
                            <view v-if="item.order_amount > 0">满{{item.order_amount}}可用</view>
                            <view v-else>无门槛</view>
                            <view>
                                <text>{{item.start_time}} 至</text>
                                <text>{{item.end_time}}</text>
                            </view>
                        </view>
                    </view>
                    <view
                        class="r"
                        @click="onBuyBonus(item.id,index)"
                    >
						<text>立即领取</text>
					</view>
                </view>
            </view>
        </view>
    </view>
</template>

<script>
    export default {
        props: {
            rdata:{
                required: true,
            }
        },
        methods:{
            onBuyBonus(id,index){
                this.$http.getCouponList({
                    id: id
                }).then(res=>{
                    if(res.status == 1){
                        this.$utils.msg(res.info);
                    }else if(res.status == '-1001'){
                        this.$router.push("/public/login");
                    }else{
                        this.$utils.msg(res.info);
                    }
                }).catch(err=>{
                    
                });
            }
        }
    }
</script>

